// =====VARS=========
$tomato-red: #cf0604;
$bg-blue: #0c0931;
$tomatoish-red: #c90303;
$logo_ff: "Slackey", cursive;
// "Bowlby One SC", cursive;
$border: 15px solid #fff;
$main_ff: "Sarpanch", sans-serif;
// $main_ff: 'Roboto Mono', monospace;
// 'Major Mono Display', monospace;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: $main_ff;
  color: #fff;
}

// =========HELPERS, COMMON ELEMS=========
.visible {
  display: block;
}
.info {
  display: none;
  font-size: 3em;
  font-family: Arial, sans-serif;
  border: 10px solid #444;
  padding: 20px;
  box-sizing: border-box;
  position: fixed;
  left: 50%;
  margin-left: -37.5%;
}
.btn {
  padding: 3px 10px;
  background-color: #656273;
  margin-bottom: 5px;
  border-radius: 5px;
  font-size: 1.1em;
  &:last-child {
    margin-bottom: 0;
  }
}

.btn {
  outline: none;
  width: 6rem;
}
// =====APP TOP LVL====
#app,
body {
  background-color: $bg_blue;
  width: 100vw;
  height: 100vh;
  .title {
    font-family: $logo_ff;
    color: $tomato_red;
    text-shadow: 1px 0px 0px gold, 1px 2px 0px #222;
    padding-left: 50px;
  }
}

// =========TIMER===========
.timer {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  font-family: monospace;
  .timer__info {
    #time-left {
      font-size: 3.4rem;
      line-height: 1;
      // letter-spacing: 5px;
    }
  }
  .timer__buttons {
    margin-left: 10px;
    justify-items: center;
    display: flex;
    flex-direction: column;
  }
}
// ========SETTINGS=====
.settings {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
.settings_header {
  flex: 1 100%;
  text-align: center;
  margin-top: 4rem;
  h3 {
    display: inline;
    padding: 0 60px;
    font-size: 1.4rem;
    font-weight: normal;
    background-image: linear-gradient(
      to right,
      transparent,
      #656273,
      rgba(200, 200, 200, 0.5),
      transparent
    );
  }
}
.session,
.break {
  display: flex;
  flex-direction: column;
  // align-items: center;
  &:last-child {
    margin-left: 15px;
  }
  button {
    outline: none;
  }
  .settings-wrap {
    display: flex;
  }
}
#session-label,
#break-label {
  // margin:  1rem 0;
  font-size: 1.3rem;
  margin-top: 1rem;
  line-height: 3;
}
#session-length,
#break-length {
  font-size: 2rem;
  padding: 0 0.3rem;
}

#break-increment,
#session-increment,
#break-decrement,
#session-decrement {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  background-color: rgba(200, 200, 200, 0.3);
  font-weight: bold;
  font-size: 1.3rem;
  line-height: 1.3;
  margin: 0px 10px;
}

@media screen and (max-width: 380px) {
  .settings {
    flex-direction: column;
    .settings_header {
      margin-bottom: 15px;
    }
    .break,
    .session {
      margin-top: 15px;
      align-self: center;
    }
  }
}

//media queries

@media screen and (min-width: 340px) {
  .settings {
    // flex-direction: column;
    text-align: center;
    &:last-child {
      margin-right: 0;
    }
    // justify-content: center;
    .session,
    .break {
      float: none;
      color: red;
    }
  }
}
